.profile-picture {
  margin : auto;
  position:relative;
  overflow:hidden;
  border-radius: 50%;
  height: 200px;
  width: 200px;
}

.profile-picture img {
  position:absolute;
  height: auto;
  width: auto;    
  max-width:100%;
  left : 0;

}

.centered img {
  position:absolute;
  top:50%;
  transform:translateY(-50%);
}

